.circular {
    /* font-size: 100px; */
    color: #fff;
    width: 111px;
    height: 111px;
    background-color: #2d3841;
    position: relative;
    border-radius: 100%;
    margin: auto;
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
    background: 0 0;
}

.circularUnfill {
    border: 7.5px solid #3a3a3a;
    width: 100%;
    height: 100%;
    position: absolute;
    border-radius: 100%;
}

.inner {
    position: absolute;
    z-index: -1;
    font-size: 45px;
    height: 100%;
    width: 100%;
    background: rgba(0, 0, 0, 0.4);
    border-radius: 100%;
    box-shadow: 0 1px 0 rgb(0 0 0 / 20%);
    line-height: 2.4em;
}

.circle {
    position: absolute;
    width: 100%;
    height: 100%;
    /* background: #fff; */
    border-radius: 100%;
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}

.bar {
    position: absolute;
    width: 100%;
    height: 100%;
    /* border: 8px solid rgb(19, 192, 33); */
    border: 8px solid #7ed321;
    border-radius: 100%;
    clip: rect(0px, 50px, 100px, 0px);
}

.fill {
    position: absolute;
    width: 100%;
    height: 100%;
    /* border: 8px solid rgb(19, 192, 33); */
    border: 8px solid #7ed321;
    border-radius: 100%;
    clip: rect(0px, 51px, 100px, 0px);
}

.circular.smaller-50 .fill {
    border-color: #2d3841;
}


